import React from 'react';

const TikTokEmbed = ({ cite, videoId, title, href, hashtag, originalMusic }) => (
  <blockquote className="tiktok-embed" cite={cite} data-video-id={videoId} style={{maxWidth: '605px', minWidth: '325px'}}>
    <section>
      <a target="_blank" title={title} href={href}>
        {title}
      </a>
      {hashtag && (
        <a title={hashtag} target="_blank" href={`https://www.tiktok.com/tag/${hashtag}?refer=embed`}>
          #{hashtag}
        </a>
      )}
      {originalMusic && (
        <a target="_blank" title="♬ som original - NextlevelDJAjuda" href={originalMusic}>
          ♬ som original - NextlevelDJAjuda
        </a>
      )}
    </section>
    <script async src="https://www.tiktok.com/embed.js"></script>
  </blockquote>
);

